import React from 'react';
import Link from 'gatsby-link';

export default(props) => {
  return (
    <Link to={ props.link } className="link__home">
      <svg className="svg-home-icon" xmlns="http://www.w3.org/2000/svg" width="50" height="50" viewBox="0 0 24 24">
          <path d="M10 20v-6h4v6h5v-8h3L12 3 2 12h3v8z"/>
          <path d="M0 0h24v24H0z" fill="none"/>
      </svg>
    </Link>
  );
};


